import { Logout } from "@mui/icons-material";
import {
  IconButton,
  ListItemIcon,
  Menu,
  MenuItem,
  Tooltip,
  Typography,
} from "@mui/material";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import CustomizedInputBase from "./search";
import KeyboardArrowRightIcon from "@mui/icons-material/KeyboardArrowRight";
function Header() {
  let navigate = useNavigate(0);
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <Container className="col-12">
      <spanner>
        <img
          onClick={(e) => {
            navigate("/globaladmissions/");
          }}
          alt="logo"
          src="/globaladmissions/images/logo.png"
        />
      </spanner>
      <SearchBar>
        <CustomizedInputBase />
      </SearchBar>
      <MenuItems>
        {/* <BellImg alt="logo" src="/globaladmissions/images/assets/bell.png" /> */}

        <Btn size="small" variant="contained mx-3">
          <span>Get Started</span>
        </Btn>

        <Tooltip title="Account settings">
          <IconButton
            onClick={handleClick}
            size="small"
            sx={{ ml: 0 }}
            aria-controls={open ? "account-menu" : undefined}
            aria-haspopup="true"
            aria-expanded={open ? "true" : undefined}
          >
            <AccountImage>
              <img
                style={{ height: "25px", width: "25px" }}
                alt="logo"
                src="/globaladmissions/images/student.png"
              />
            </AccountImage>
          </IconButton>
        </Tooltip>

        <Menu
          anchorEl={anchorEl}
          id="account-menu"
          open={open}
          onClose={handleClose}
          onClick={handleClose}
          PaperProps={{
            elevation: 0,
            sx: {
              overflow: "visible",
              filter: "drop-shadow(0px 2px 8px rgba(0,0,0,0.32))",
              mt: 1.5,
              "& .MuiAvatar-root": {
                width: 32,
                height: 32,
                ml: -0.5,
                mr: 1,
              },
              "&:before": {
                content: '""',
                display: "block",
                position: "absolute",
                top: 0,
                right: 14,
                width: 10,
                height: 10,
                bgcolor: "background.paper",
                transform: "translateY(-50%) rotate(45deg)",
                zIndex: 0,
              },
            },
          }}
          transformOrigin={{ horizontal: "right", vertical: "top" }}
          anchorOrigin={{ horizontal: "right", vertical: "bottom" }}
        >
          <MenuItem>
            <KeyboardArrowRightIconCustom />
            <Typography className="px-2" variant="subtitle2">
              My Account
            </Typography>
          </MenuItem>

          <MenuItem>
            <ListItemIcon>
              <Logout fontSize="small" />
            </ListItemIcon>
            Logout
          </MenuItem>
        </Menu>
      </MenuItems>
    </Container>
  );
}

export default Header;

const KeyboardArrowRightIconCustom = styled(KeyboardArrowRightIcon)`
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 50%;
  color: rgba(0, 0, 0, 0.3);
`;

// const BellImg = styled.img`
//   cursor: pointer;
// `;

const AccountImage = styled.div`
  height: 35px;
  width: 35px;
  border: 1px solid rgba(0, 0, 0, 0.09);
  cursor: pointer;
  padding: 3px;
  background: url(/globaladmissions/images/student.png);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0px 3px;
`;

const MenuItems = styled.div`
  display: flex;
  align-items: space-around;
  align-items: center;
  @media (max-width: 800px) {
    display: none;
    width: 0vw;
  }
`;

const Btn = styled.button`
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 5px 24px 5px;
  gap: 10px;
  border: none;
  background: radial-gradient(
    62.26% 123.96% at 69.08% 44.79%,
    #1d4ed8 0%,
    #1741b7 100%
  );
  box-shadow: 0px 4px 8px rgba(2, 14, 47, 0.1), inset 0px 1px 0px #1741b7,
    inset 0px 2px 0px rgba(255, 255, 255, 0.13),
    inset -1px -1px 0px rgba(5, 17, 57, 0.1);
  border-radius: 8px;
  span {
    color: white;
  }
`;

const SearchBar = styled.div`
  flex: 0.9;
`;

const Container = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 6px;

  img {
    cursor: pointer;
  }
`;
